<template>
  <div>
    <div class="gp-dashboard">
      <h1 class="content-header">{{ this.$route.name }}</h1>
      <base-header class="pb-6" type="primary"></base-header>

      <div class="container-fluid mt--5">
        <div class="row">
          <div class="col">
            <div class="card border-0">
              <div
                id="map-custom"
                class="map-canvas"
                style="height: calc(100vh - 512px); min-height: 350px"
              ></div>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col-12">
            <div class="card border-0">
              <div class="card-header">
                <div class="row align-items-center">
                  <div class="col-6">
                    <h2 class="m-0">Rekapitulasi Green Port</h2>
                  </div>
                  <div class="col-6 text-right">
                    <el-select
                      size="mini"
                      v-model="modal.periode"
                      placeholder="Pilih Periode"
                      @change="getData(9999, modal.periode)"
                    >
                      <el-option
                        v-for="option in periode"
                        :key="option.value"
                        :label="option.label"
                        :value="option.value"
                      ></el-option>
                    </el-select>
                  </div>
                </div>
              </div>
              <!-- Card body -->
              <div class="dashboard card-body overflow-auto">
                <div class="col">
                  <div class="row" v-for="unit in tableData" :key="unit.id">
                    <div class="col-11 pl-0">
                      <h3 class="title text-muted">{{ unit.unitName }}</h3>
                      <base-progress
                        :height="10"
                        type="danger"
                        :value="ratingHandler(unit)"
                      ></base-progress>
                    </div>
                    <div class="col-1 p-0">
                      <div
                        class="container-fraction bg-gray d-flex align-items-center justify-content-center text-align-center h-100 w-100 text-white rounded"
                      >
                        <p class="m-0">{{ ratingHandler(unit).toFixed(2) }}%</p>
                      </div>
                    </div>
                    <div
                      class="col-12"
                      style="
                        border-top: 2px solid lightgray;
                        margin: 30px auto 15px auto;
                        padding: 10;
                      "
                    ></div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Select, Option } from "element-ui";
import BaseButton from "@/components/BaseButton";
import BaseProgress from "@/components/BaseProgress";
import GoogleMapsLoader from "google-maps";
import markerContent from "../IEMS/marker";
import { API_KEY } from "@/views/Maps/API_KEY.js";
import axios from "axios";

GoogleMapsLoader.KEY = API_KEY;

const intRNG = (min, max) => {
  return Math.floor(Math.random() * (max - min) + min);
};

export default {
  components: {
    BaseProgress,
    BaseButton,
    [Select.name]: Select,
    [Option.name]: Option,
  },
  data() {
    return {
      renders: {
        loading: true,
      },
      api: this.$store.state.api,
      modal: {
        periode: null,
      },
      periode: null,
      mapData: [],
      tableData: [],
      count: {
        kp: 23,
        pg: 16,
        pl: 14,
        mk: 12,
        ku: 26,
        kl: 11,
        at: 6,
        dp: 17,
        dk: 4,
        em: 17,
        ai: 12,
        hs: 8,
        ml: 17,
        lb: 32,
        sb: 6,
      },
      max: {
        kp: 15,
        pg: 10,
        pl: 1.5,
        mk: 1.5,
        ku: 6,
        kl: 6,
        at: 6,
        dp: 6,
        dk: 5,
        em: 25,
        ai: 7.5,
        hs: 1.5,
        ml: 1.5,
        lb: 6,
        sb: 1.5,
      },
      portion: {
        kp: [0.2, 0.2, 0.1, 0.1, 0.2, 0.2],
        pg: [0.3, 0.3, 0.4],
        pl: [0.15, 0.15, 0.125, 0.125, 0.2, 0.125, 0.125],
        mk: [0.3, 0.4, 0.3],
        ku: [0.1, 0.1, 0.05, 0.05, 0.2, 0.25, 0.05, 0.2],
        kl: [0.3, 0.3, 0.3, 0.1],
        at: [0.15, 0.2, 0.15, 0.2, 0.15, 0.15],
        dp: [0.1, 0.1, 0.1, 0.05, 0.05, 0.1, 0.1, 0.4],
        dk: [0.65, 0.35],
        em: [0.2, 0.6, 0.2],
        ai: [0.1, 0.1, 0.3, 0.5],
        hs: [0.15, 0.3, 0.2, 0.2, 0.15],
        ml: [0.2, 0.1, 0.1, 0.2, 0.4],
        lb: [0.2, 0.075, 0.1, 0.075, 0.1, 0.075, 0.1, 0.075, 0.2],
        sb: [0.4, 0.3, 0.3],
      },
      start: {
        kp: [0, 5, 8, 12, 15, 20],
        pg: [0, 5, 10],
        pl: [0, 1, 2, 5, 6, 7, 9],
        mk: [0, 3, 8],
        ku: [0, 4, 7, 9, 12, 16, 19, 20],
        kl: [0, 2, 5, 7],
        at: [0, 1, 2, 3, 4, 5],
        dp: [0, 1, 2, 3, 4, 5, 6, 7],
        dk: [0, 2],
        em: [0, 6, 14],
        ai: [0, 3, 5, 10],
        hs: [0, 1, 3, 5, 7],
        ml: [0, 3, 4, 5, 6],
        lb: [0, 11, 13, 16, 20, 21, 23, 25, 27],
        sb: [0, 2, 4],
      },
      span: {
        kp: [5, 3, 4, 3, 5, 3],
        pg: [5, 5, 6],
        pl: [1, 1, 3, 1, 1, 2, 5],
        mk: [3, 5, 4],
        ku: [4, 3, 2, 3, 4, 3, 1, 6],
        kl: [2, 3, 2, 4],
        at: [1, 1, 1, 1, 1, 1],
        dp: [1, 1, 1, 1, 1, 1, 1, 10],
        dk: [2, 2],
        em: [6, 8, 3],
        ai: [3, 2, 5, 2],
        hs: [1, 2, 2, 2, 1],
        ml: [3, 1, 1, 1, 11],
        lb: [11, 2, 3, 4, 1, 2, 2, 2, 5],
        sb: [2, 2, 2],
      },
    };
  },
  methods: {
    periodeRNG() {
      return this.periode[intRNG(0, this.periode.length - 1)].PERIODE;
    },
    getListPeriode() {
      axios
        .get("http://45.127.133.75:3000/api/iems/list-periode")
        .then((response) => {
          let array = [];
          for (let index = 0; index < response.data.length; index++) {
            array.push({
              label: response.data[index].PERIODE,
              value: response.data[index].PERIODE,
            });
          }
          array.sort((a, b) => {
            return -1;
          });
          this.periode = array;
          this.modal.periode = this.periode[0].value; // harusnya this.periode[0].value
          this.getData(9999, this.modal.periode);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getData(unit, periode) {
      if (this.$store.getters.isDebugging) {
        console.log(
          "DEBUG: Getting rating list on ",
          this.api.url + this.api.gp.getDashboard + periode
        );
      }
      axios
        .get(this.api.url + this.api.gp.getDashboard + periode)
        .then((response) => {
          if (this.$store.getters.isDebugging) {
            console.log(
              "DEBUG: Success to get rating list. Data: ",
              response.data
            );
          }
          let array = [];
          for (let i = 0; i < response.data.length; i++) {
            array.push(this.passDATA(response.data[i]));
          }
          array.sort((a, b) => {
            let aVal = a.unitName;
            let bVal = b.unitName;
            if (aVal < bVal) {
              return -1;
            }
            if (aVal > bVal) {
              return 1;
            }
            return 0;
          });
          this.tableData = array;
          this.getMap();
          this.renders.loading = false;
        })
        .catch((error) => {
          if (this.$store.getters.isDebugging) {
            console.log("ERROR: Failed to get rating list. ", error);
          }
        });
    },
    passDATA(data) {
      var container = {};
      container.id = data.GP_ID;
      container.timestamp = data.CREATEDAT / 1000;
      container.unitName = data.UK_NAME;
      container.kp = data.KP;
      container.pg = data.PG;
      container.pl = data.PL;
      container.mk = data.MK;
      container.ku = data.KU;
      container.kl = data.KL;
      container.at = data.AT;
      container.dp = data.DP;
      container.dk = data.DK;
      container.em = data.EM;
      container.ai = data.AI;
      container.hs = data.HS;
      container.ml = data.ML;
      container.lb = data.LB;
      container.sb = data.SB;
      return container;
    },
    getMap() {
      axios.get("http://45.127.133.75:3000/api/iems/map").then((res) => {
        this.mapData = res.data;
        GoogleMapsLoader.load((google) => {
          this.initMap(google);
        });
      });
    },
    initMap(google) {
      let map,
        lat = 0.340199,
        lng = 116.472123,
        color = "#5e72e4";
      map = document.getElementById("map-custom");

      let centerLat = new google.maps.LatLng(0.340199, 116.472123);
      let mapOptions = {
        zoom: 5,
        scrollwheel: false,
        center: centerLat,
        mapTypeId: google.maps.MapTypeId.ROADMAP,
      };

      map = new google.maps.Map(map, mapOptions);
      var infowindow = null;
      for (let i = 0; i < this.mapData.length; i++) {
        let pos = new google.maps.LatLng(
          this.mapData[i].UK_LAT,
          this.mapData[i].UK_LONG
        );
        let url = "http://maps.google.com/mapfiles/ms/icons/orange-dot.png";
        let blueDot = "http://maps.google.com/mapfiles/ms/icons/blue-dot.png";

        // url += markerContent[i].icon;
        let marker = [];
        marker[i] = new google.maps.Marker({
          icon: { url: url },
          position: pos,
          map: map,
          animation: google.maps.Animation.DROP,
          title:
            this.mapData[i].UK_NAME +
            " " +
            this.ratingHandler(this.tableData[i]).toFixed(2) +
            "%",
        });
        const that = this;
        google.maps.event.addListener(marker[i], "click", function () {
          let content_ =
            '<div class="fs-16 mx-auto info-window-content"><h2>' +
            that.mapData[i].UK_NAME +
            "</h2>" +
            that.getPemenuhanMap(that.mapData[i].UK_NAME);

          if (infowindow) {
            infowindow.close();
          }
          infowindow = new google.maps.InfoWindow({
            content: content_,
          });
          infowindow.open(map, marker[i]);
        });
      }
    },
    ratingHandler(data) {
      let aspect = {
        kp: 0,
        pg: 0,
        pl: 0,
        mk: 0,
        ku: 0,
        kl: 0,
        at: 0,
        dp: 0,
        dk: 0,
        em: 0,
        ai: 0,
        hs: 0,
        ml: 0,
        lb: 0,
        sb: 0,
      };
      Object.keys(aspect).forEach((key) => {
        aspect[key] = this.aspectHandler(key, data[key]);
      });

      return this.recaptHandler(aspect);
    },
    aspectHandler(aspect, data) {
      let value = 0;

      for (let i = 0; i < this.portion[aspect].length; i++) {
        value = this.percentageHandler(aspect, data);
      }
      return value;
    },
    percentageHandler(aspect, data) {
      let percentage = [];

      for (let i = 0; i < this.span[aspect].length; i++) {
        let sum = 0;
        for (
          let j = this.start[aspect][i];
          j < this.start[aspect][i] + this.span[aspect][i];
          j++
        ) {
          sum += Math.abs(data[j]);
        }
        percentage.push(
          (sum / (this.span[aspect][i] * 2)) * this.portion[aspect][i] * 100
        );
      }

      let total = 0;
      for (let i = 0; i < percentage.length; i++) {
        total += percentage[i];
      }

      return total;
    },
    recaptHandler(aspect) {
      Object.keys(aspect).forEach((key) => {
        aspect[key] *= this.max[key] / 100;
      });

      let recapt = 0;
      Object.values(aspect).forEach((value) => {
        recapt += value;
      });
      return recapt;
    },
  },
  created() {
    this.getListPeriode();
  },
  mounted() {
    // this.getMap();
  },
};
</script>

<style lang="scss" scoped>
.gp-dashboard {
  .dashboard {
    height: 280px;
    &::-webkit-scrollbar {
      display: none;
    }
    -ms-overflow-style: none;
    scrollbar-width: none;
  }
  .container-fraction {
    border-radius: 2rem;
  }
}
</style>

<style lang="scss">
.gp-dashboard {
  .progress {
    margin: 0;
    border-radius: 2rem;
  }
}
</style>
